.header {
    height: 70px;
    padding: 0 15px;
    justify-content: flex-start;
    align-items: center;
}

.header__headline {
    width: 30%;
}

.header__headline a {
    color: white;
    text-decoration: none;
}

.header__nav {
    list-style: none;
    padding: 0;
    margin: 0;
}

.header__nav-item {
    width: content-box;
    padding: 0 10px;
}

.header__nav-item a {
    text-decoration: none;
}
@keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@-webkit-keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}
@-webkit-keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}
.lds-double-ring {
    position: relative;
}
.lds-double-ring div {
    position: absolute;
    width: 160px;
    height: 160px;
    top: 20px;
    left: 20px;
    border-radius: 50%;
    border: 8px solid #000;
    border-color: #00bc8c transparent #00bc8c transparent;
    -webkit-animation: lds-double-ring 1.8s linear infinite;
    animation: lds-double-ring 1.8s linear infinite;
}
.lds-double-ring div:nth-child(2) {
    width: 140px;
    height: 140px;
    top: 30px;
    left: 30px;
    border-color: transparent #fff transparent #fff;
    -webkit-animation: lds-double-ring_reverse 1.8s linear infinite;
    animation: lds-double-ring_reverse 1.8s linear infinite;
}
.lds-double-ring {
    width: 66px !important;
    height: 66px !important;
    -webkit-transform: translate(-33px, -33px) scale(0.33) translate(33px, 33px);
    transform: translate(-33px, -33px) scale(0.33) translate(33px, 33px);
}
.error-indicator {
    width: content-box;
    margin: 0 auto;
    text-align: center;
}

.error-icon {
    width: 100px;
    height: 100px;
    margin-bottom: 5px;
}

.error-text {
    margin: 0;
}
.card {
    min-height: 200px;
    margin-bottom: 30px;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 15px;
    border: 1px solid #444;
}

.planet-image,
.card-image {
    max-height: 170px;
    max-width: 170px;
    display: block;
    border-radius: 5px;
}

.card-body {
    width: 80%;
}

.list-group-item {
    height: 35px;
    padding: 5px;
    margin-left: 20px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.list-group-item:first-child{
    border-top: none;
}

.term {
    margin-right: 5px;
}

.lds-css {
    margin: 0 auto;
}
.item-list-item {
    margin: 0;
    width: 100%;
    min-height: 2.5rem;
    padding: 10px;
}

.item-list-item:hover{
    background-color: #4d5151;
    cursor: pointer;
}

.item-list-item:active{
    background-color: #3b3b3b;
}



.stardb-app {
    width: 80%;
    margin: 0 auto;
}

.left-col {
    margin-bottom: 10px;
}
